@import url(https://fonts.googleapis.com/css?family=Fira+Sans);
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
:root{--color-red: #EC1F27;--color-white: white;--color-black: #222}body{font-family:'Fira Sans', sans-serif}.Page{width:100vw;height:100vh;overflow:hidden auto;display:flex;flex-direction:column}.Header{display:flex;flex-direction:row;justify-content:space-between;padding:12px;background-color:black;box-shadow:5px 5px 15px 5px rgba(0,0,0,0.05);font-size:22px;color:white}.Header .img-holder{display:flex}.Header .img-holder img{width:96px;height:96px;border-radius:48px}.Header .img-holder span{align-self:center;text-transform:uppercase;font-size:24px;padding-left:16px}.Header .link-holder{display:flex}.Header .link-holder a{align-self:center;text-decoration:none;text-transform:capitalize;color:inherit;padding-left:16px}.Footer{display:flex;flex-direction:column;justify-content:space-between;padding:12px;background-color:black;box-shadow:5px 5px 15px 5px rgba(0,0,0,0.05);font-size:48px;color:white}.Footer .img-holder{display:flex;justify-content:center;padding:32px}.Footer .img-holder img{width:240px;height:240px;border-radius:120px}.Footer .hashtags{display:flex;flex-direction:column;padding-bottom:32px}.Footer .hashtags *{align-self:center}.Footer .acknowledgements{display:flex;flex-direction:column;font-size:12px;color:#aaa}.Footer .acknowledgements *{align-self:center}.Home{display:flex;flex-direction:column}.Home>:not(.hero-image){padding-left:32px;padding-right:32px;max-width:1000px;align-self:center}.Home .summary{width:calc(100% - 64px)}.Home .summary img{width:300px}.Home p{line-height:200%;padding-bottom:22px}.Home .hero-image{min-height:220px;background-attachment:fixed;background-position:center;background-repeat:no-repeat;background-size:cover;display:flex;flex-direction:column;color:white}.Home .hero-image h1{margin-top:64px}.Home .hero-image *{align-self:center}.Home .hero-image .MuiButtonBase-root{background-color:#EC1F27;background-color:var(--color-red);color:white;font-weight:800;font-size:22px}.Home li{padding-bottom:24px}.flex-row{display:flex;flex-direction:row}.flex-column{display:flex;flex-direction:column}.bold{font-weight:bold}

