@import url(https://fonts.googleapis.com/css?family=Fira+Sans);

:root {
  --color-red: #EC1F27;
  --color-white: white;
  --color-black: #222;
}

body {
  font-family: 'Fira Sans', sans-serif;
}

.Page {
  width: 100vw;
  height: 100vh;
  overflow: hidden auto;
  display: flex;
  flex-direction: column;
}

.Header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 12px;
  background-color: black;
  box-shadow: 5px 5px 15px 5px rgba(0,0,0, 0.05);
  font-size: 22px;
  color: white;

  & .img-holder {
    display: flex;
    & img {
      width: 96px;
      height: 96px;
      border-radius: 48px;
    }

    & span {
      align-self: center;
      text-transform: uppercase;
      font-size: 24px;
      padding-left: 16px;
    }
  }

  & .link-holder {
    display: flex;
    & a {
      align-self: center;
      text-decoration: none;    
      text-transform: capitalize;
      color:inherit;
      padding-left: 16px;
    }
  }
}

.Footer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 12px;
  background-color: black;
  box-shadow: 5px 5px 15px 5px rgba(0,0,0, 0.05);
  font-size: 48px;
  color: white;
  & .img-holder {
    display: flex;
    justify-content: center;
    padding:32px;
    & img {
      width: 240px;
      height: 240px;
      border-radius: 120px;
    }
  }

  & .hashtags {
    display: flex;
    flex-direction: column;
    padding-bottom: 32px;
    & * {
      align-self: center;
    }
  }

  & .acknowledgements {
    display: flex;
    flex-direction: column;
    font-size: 12px;
    color: #aaa;
    & * {
      align-self: center;
    }
  }
}

.Home {
  display: flex;
  flex-direction: column;
  & > :not(.hero-image) {
    padding-left: 32px;
    padding-right: 32px;
    max-width: 1000px;
    align-self: center;
  }

  & .summary {
    width: calc(100% - 64px);
    & img {
      width: 300px;
    }
  }

  & p {
    line-height: 200%;
    padding-bottom: 22px;
  }

  & .hero-image {
    min-height: 220px;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    display: flex;
    flex-direction: column;
    color: white;

    & h1 {
      margin-top:64px;
    }
    & * {
      align-self: center;
    }
    & .MuiButtonBase-root {
      background-color: var(--color-red);
      color: white;
      font-weight: 800;
      font-size: 22px;
    }
  }

  & li {
    padding-bottom: 24px;
  }
}

.flex-row {
  display: flex;
  flex-direction: row;
}

.flex-column {
  display: flex;
  flex-direction: column;
}

.bold {
  font-weight: bold;
}